.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }

.LoadingTodo-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}

.LoadingTodo-text {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}

.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
  border-radius: 50px;
  height: 48px;
  width: 48px;
}

.LoadingTodo-completeIcon {
  position: absolute;
  left: 12px;
}

.LoadingTodo-deleteIcon {
  position: absolute;
  top: -24px;
  right: 0;
}

.LoadingTodo-container,
.LoadingTodo-completeIcon,
.LoadingTodo-deleteIcon {
  background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200, 199, 199));
  background-size: 400% 400%;
  -webkit-animation: loadingAnimation 3s ease-in-out infinite;
          animation: loadingAnimation 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

form {
    width: 90%;
    max-width: 300px;
    background: #eef2f3 ;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 52px;
    width: calc(100% - 25px);
  }
  
  textarea::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
  }
  
  .TodoForm-button--add {
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border-radius: 24px;
  }

  .TodoForm-button--add:hover {
    background: #98DAFA;
  }
  
  .TodoForm-button--cancel {
    background: #EC407A;
    border-radius: 24px;
  }

  .TodoForm-button--cancel:hover {
    background: #EC607A;
  }
.TodoCounter {
  font-size: 24px;
  text-align: center;
  margin: 0;
  padding: 24px;
}

.TodoCounter--loading {
  opacity: 25%;
}

.TodoSearch {
  background: transparent;
  border-radius: 20px;
  border: 2px solid #202329;
  margin: 0 24px;
  height: 32px;
  width: calc(100% - 62px);
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  outline: transparent;
}

.TodoSearch::-webkit-input-placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 100;
  font-size: medium;
}

.TodoSearch::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 100;
  font-size: medium;
}

.TodoSearch:focus {
  outline-color: #2a87a1;
}

.TodoSearch--loading{
  opacity: 25%;
}

ul {
  margin: 0;
  padding: 0 0 56px 0;
  list-style: none;
}

.TodoItem {
  background: linear-gradient(to left,#71B280, #71B280); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position: relative;   
  display: flex;
  justify-content: center; 
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  border-radius: 20px;
  height: 54px;
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 100px); 
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon-container { 
    cursor: pointer;
    display: flex;
    justify-content: center;  
    align-items: center;
    height: 48px;
    width: 48px;
    /* background-color: #CCC; */
  }
  .Icon-container--check {
    position: absolute;
    left: 12px;
  }
  .Icon-container--delete {
    position: absolute;
    /* top: -24px; */
    right: 12px;
  }

  .Icon-container--edit {
    position: absolute;
    /* top: -24px; */
    right: 48px;
  }
  
  .Icon-svg {
    width: 24px;
    height: 24px;
    transition: fill .15s;
  }
  .Icon-container:hover .Icon-svg--check {
    fill: gray;
  }
  .Icon-container:hover .Icon-svg--delete {
    fill: red;
  }


  
.CreateTodoButton-container {
  display: flex;
  align-items: center;
  margin: 16px 0 16px 0;
}
.CreateTodoButton {
  background: #2C5364;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
  color: #FAFAFA;
  justify-content: center;
  align-items: center;
  height: 44px;
  width: 44px;
  -webkit-transform: rotate(0);
          transform: rotate(0);
  transition: .3s ease;
  margin-left: 16px;
}

.CreateTodoButton:hover {
  -webkit-transform: rotate(224deg);
          transform: rotate(224deg);
}

.ChangeAlert-bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #1e1e1f50;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}
  
.ChangeAlert-container {
    width: 250px;
    padding: 34px;
    background-color: #1e1e1f;
    color: #f2f4f5;
    z-index: 2;
    text-align: center;
    font-size: 20px;
    border-radius: 8px;
}
  
.ChangeAlert-container button {
    margin-top: 16px;
    margin-bottom: 16px;
}
  
body {
  margin: 0;
  color: #1A5276;
