.Icon-container { 
    cursor: pointer;
    display: flex;
    justify-content: center;  
    align-items: center;
    height: 48px;
    width: 48px;
    /* background-color: #CCC; */
  }
  .Icon-container--check {
    position: absolute;
    left: 12px;
  }
  .Icon-container--delete {
    position: absolute;
    /* top: -24px; */
    right: 12px;
  }

  .Icon-container--edit {
    position: absolute;
    /* top: -24px; */
    right: 48px;
  }
  
  .Icon-svg {
    width: 24px;
    height: 24px;
    transition: fill .15s;
  }
  .Icon-container:hover .Icon-svg--check {
    fill: gray;
  }
  .Icon-container:hover .Icon-svg--delete {
    fill: red;
  }


  